@import "_var.scss";
@import "_components.scss";






/*min-height:32vh;*/

header {
	width: 100%;
	padding: 10px 0 0 0;
	background-color: #18191A;
	z-index: 11;
	position: relative;
	& .nav-bar {
		//max-width: 1366px;
		margin: 0 auto;
		padding: 20px 53px;
		border-bottom: 0.5px solid rgba(197, 190, 215, 0.20);
		& .menu {
			display: grid;
			grid-template-columns: 1fr minmax(1px,1100px) 1fr;
			align-items: center;
			& .logo {
				padding-left: 20px;
				width: 250px;
				& .item--logo {
					width: 30px;
					height: 30px;
				}
			}
			& .navigation {
				display: grid;
				& ul {
					padding:0;
					& li {
						display: inline-block;
						padding-right: 30px;
						& a {
							font-family: 'SF UI Display', serif;
							font-style: normal;
							font-weight: bold;
							font-size: 17px;
							line-height: 20px;
							align-items: center;
							letter-spacing: -0.5px;
							color: white;
						}
					}
				}
			}
			& .right__panel {
				display: grid;
				grid-template-columns: 1fr 1fr 1fr 1fr;
				align-items: center;
				& .theme__swiper{
					& .theme__swiper_inner {
						width: 50px;
						height: 31px;
						background: #868686;
						border-radius: 112px;
						display: grid;
						align-items: center;
						position: relative;
						cursor: pointer;
						& .switch__item {
							width: 24px;
							height: 24px;
							background: #181919;
							box-shadow: 0 2px 2px rgba(0, 0, 0, 0.4);
							border-radius: 50%;
							position: absolute;
							right: 3px;
							display: grid;
							justify-items: center;
							align-items: center;
							& img {
								width: 55%;
							}
							& .item-black{
								display: none;
							}
						}
					}
					padding: 0 10px;
				}
				& .language__menu {
					padding: 0 10px;
					font-family: 'SF UI Display', serif;
					font-style: normal;
					font-weight: 600;
					font-size: 17px;
					line-height: 20px;
					letter-spacing: -0.5px;
					color: white;
					display: grid;
					align-items: center;
					grid-template-columns: max-content min-content;
					cursor: pointer;
					& img {
						//padding-left: 5px;
					}
					& p {
						width: 20px;
						margin-right: 10px;
					}
				}
				& .enter--area {
					padding: 0 10px;
					& a {
						font-family: 'SF UI Display', serif;
						font-style: normal;
						font-weight: 600;
						font-size: 17px;
						line-height: 20px;
						letter-spacing: -0.5px;
						color: white;
					}
				}
				& .contact {
					padding: 0 10px;
					& .btn--start{
						width: 136px;
						height: 40px;
						background: #FF6600;
						border-radius: 4px;
						display: grid;
						& a {
							width: 100%;
							height: 100%;
							display: grid;
							justify-items: center;
							align-items: center;
							font-family: 'SF UI Display', serif;
							font-style: normal;
							font-weight: 600;
							font-size: 17px;
							line-height: 20px;
							letter-spacing: -0.5px;
							color: white;
						}
					}
				}
			}
		}
	}
	& .bottom__navigation {
        padding: 20px 0;
		border-bottom: 0.5px solid rgba(197, 190, 215, 0.20);
		& .navigation__inner {
			max-width: 1103px;
			margin: 0 auto;
			& ul {
				padding:0;
				& li {
					display: inline-block;
					padding-right: 30px;
					& a {
						font-family: 'SF UI Display', serif;
						font-style: normal;
						font-weight: bold;
						font-size: 20px;
						line-height: 24px;
						display: flex;
						align-items: center;
						letter-spacing: -0.5px;
						color: #FFFFFF;
						opacity: 0.4;
					}
					& .active-menu {
						opacity: 1;
						position: relative;
						&::before {
							position: absolute;
							top: 57px;
							left: 0;
							width: 100%;
							height: 6px;
							background-color: #FF6600;
							content: '';
						}

					}
				}
			}
		}
	}
}


@media only screen and (max-width : 1360px) {
   header {
	   & .nav-bar {
		   padding: 20px;
		   & .menu {
			   & .navigation {
				   display: none;
			   }
			   & .logo{
				   padding: 0;
			   }
			   & .right__panel {
				   display: none;
			   }
			   & .contact{
				   display: none;
			   }
			   & .burger-menu {
				   display: grid;
				   justify-self: end;
				   width: 20px;
				   height: 20px;
				   position: relative;
				   & span {
					   cursor: pointer;
					   border-radius: 10px;
					   height: 4px;
					   width: 30px;
					   background: white;
					   position: absolute;
					   display: block;
					   content: '';
					   right: 0;
					   &::before {
						   top: 16px;
						   cursor: pointer;
						   border-radius: 20px;
						   height: 4px;
						   width: 30px;
						   background: white;
						   position: absolute;
						   display: block;
						   content: '';
						   right: 0;
					   }
					   &::after {
						   cursor: pointer;
						   border-radius: 20px;
						   height: 4px;
						   width: 30px;
						   background: white;
						   position: absolute;
						   display: block;
						   content: '';
						   right: 0;
						   top: 8px;
					   }
				   }
			   }
		   }
	   }
	   & .bottom__navigation {
		   padding: 20px 20px;
		   & .navigation__inner {
			   display: grid;
			   justify-items: center;
		   }
	   }
   }
}
@media only screen and (max-width : 800px) {
	header .bottom__navigation .navigation__inner ul li a {
		font-size: 16px;
	}
	header {
		& .bottom__navigation {
			& .navigation__inner {
				max-width: 1103px;
				margin: 0 auto;
				box-sizing: content-box;
				overflow: hidden;
				height: 24px;

				& ul {
					white-space: nowrap;
					overflow-x: scroll;
					width: 100%;
					padding: 0;
					height: 44px;
					margin: 0;
					overflow-y: hidden;

					& li {
						display: inline-block;
						padding-right: 30px;
						padding-bottom: 20px;

					}
				}
			}
		}
	}
}